<script>
export default {
  name: 'App',
  data() {
    return {
      currentTime: '',
    };
  },
  mounted() {
    this.updateTime();
    setInterval(this.updateTime, 1000);

    /* global particlesJS */
    particlesJS('particles-js', {
      particles: {
        number: { value: 80, density: { enable: true, value_area: 800 } },
        color: { value: '#ffffff' },
        shape: {
          type: 'circle',
          stroke: { width: 0, color: '#000000' },
        },
        opacity: {
          value: 0.5,
          random: false,
        },
        size: {
          value: 3,
          random: true,
        },
        line_linked: {
          enable: true,
          distance: 150,
          color: '#ffffff',
          opacity: 0.4,
          width: 1,
        },
        move: {
          enable: true,
          speed: 3,
          direction: 'none',
          random: false,
          straight: false,
          out_mode: 'out',
          bounce: false,
        },
      },
      interactivity: {
        detect_on: 'canvas',
        events: {
          onhover: { enable: true, mode: 'repulse' },
          onclick: { enable: true, mode: 'push' },
          resize: true,
        },
        modes: {
          repulse: { distance: 100, duration: 0.4 },
          push: { particles_nb: 4 },
        },
      },
      retina_detect: true,
    }
  );
  },
  methods: {
    updateTime() {
      const now = new Date();
      const format = (n) => (n < 10 ? '0' + n : n);
      const h = format(now.getHours());
      const m = format(now.getMinutes());
      const s = format(now.getSeconds());
      const y = now.getFullYear();
      const mo = format(now.getMonth() + 1);
      const d = format(now.getDate());
      this.currentTime = `${y}-${mo}-${d} ${h}:${m}:${s}`;
    }
  }
}

</script>

